import thumb01 from '../assets/images/thumbs_ML/kit-full.jpg'
import thumb02 from '../assets/images/thumbs_ML/kit-full 2.jpg'
import thumb03 from '../assets/images/thumbs_ML/kit-full 3.jpg'
